
.flash {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #ba1d2d;
  color: #fff;
  overflow: hidden;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}
.flash.reverse {
    background-color: #fff;
    color: #ba1d2d;
}
.flash .play-btn {
    width: 200px;
    height: 50px;
    color: #fff;
    font-size: 2em;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -100px;
    margin-top: -25px;
    z-index: 9;
}
.flash .frame {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.flash .frame span {
      display: inline-block;
}
.flash .frame span.block-span {
        display: block;
        line-height: 0.8em;
}
.flash .frame .snow-container {
      font-size: 1.5em;
      text-align: center;
}
.flash .frame .snow-container .snow {
        margin: 3px;
}
.flash .frame .line-container {
      display: table;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
}
.flash .frame .line-container .line {
        text-align: center;
        display: table-cell;
        vertical-align: middle;
        font-size: 4em;
}
.flash .frame .line-container .line.bg-line {
          background-color: #ba1d2d;
          background-clip: content-box;
}



